  .filter-wrapper {
    overflow-x: auto;
  }
  
  .filter-nav {
    display: flex;
    gap: 0.5rem;
    list-style: none;
  }
  
  .filter-nav {
    display: flex;
    overflow-x: auto;
    padding: 1rem;
    gap: 0.5rem;
  }
  
  .filter-nav::-webkit-scrollbar {
    display: none; 
  }
  
  .filter-item {
    padding: 0.5rem 1rem;
    background-color: #b5b5b5;
    color: #333;
    font-weight: bold;
    border-radius: 999px;
    white-space: nowrap;
    cursor: pointer;
    transition: background-color 0.2s ease, color 0.2s ease;
    user-select: none;
  }
  
  .filter-item:hover {
    background-color: #e0dfdf;
  }
  
  .filter-item.active {
    color: black;
  }